











import { Component, Vue, Prop } from 'vue-property-decorator';
import { db } from "@/api/firebase";
import cuid from "cuid";
import _ from "lodash";

@Component
export default class AddNew extends Vue {
    private nw: any = {}
    private readonly pId:string  = cuid()
     
    private async saveToDB() {
        if (!_.isEmpty(this.nw)) {
            this.nw.status = 'ACTIVE'
            db.collection('prodc').doc(this.pId).set(this.nw,{merge:true})
            .then(()=>{
                this.nw = {}
                db.collection("prodc")
                .get()
                .then(snap => {
                    this.$store.dispatch("fetchNewData", snap);
                });
            })
        }
    }

}
